<template>
  <div>
    <p>number<el-input type="number" v-model="dataNumber"/> </p>
    <p>string<el-input type="text" v-model="dataString"/>  </p>
    <p>array<el-input type="text" v-model="dataArray[1]"/> </p>
    <p>Object<el-input type="text" v-model="dataObject.name"/> </p>
  </div>
</template>
<script>
export default {
  props: ['dataNumber', 'dataString', 'dataArray', 'dataObject']
}
</script>
<style lang="scss" scoped>
.el-input{
  display: inline-block;
  width: 200px;
}
</style>



