<template>
  <div>
    <div class="title">
      <span></span>
      <div class="animation">
        <div class="move"></div>
      </div>
    </div>
    <h4></h4>
    <pre>
      

      stack overflow
      
    </pre>
    <el-divider></el-divider>
    <h4></h4>
    <pre>
      function factorial(n) {
        if (n === 1) return 1;
        return n * factorial(n - 1);
      }

      factorial(5) // 120

      ---------------------------------------------------------

      nn O(n) 

       O(1) 

      ---------------------------------------------------------

      function factorial(n, total) {
        if (n === 1) return total;
        return factorial(n - 1, n * total);
      }

      factorial(5, 1) // 120
    </pre>
    <el-divider></el-divider>
    <h4></h4>
    <pre>
      

      function trampoline(f) {
        while (f && f instanceof Function) {
          f = f();
        }
        return f;
      }

      trampoline(func)   //  func

      ---------------------------------------------------------

      sum
      
    </pre>
    <el-divider></el-divider>
    <h4></h4>
    <pre>
      function tco(f) {
        var value;
        var active = false;
        var accumulated = [];

        return function accumulator() {
          accumulated.push(arguments);
          if (!active) {
            active = true;
            while (accumulated.length) {
              value = f.apply(this, accumulated.shift());
            }
            active = false;
            return value;
          }
        };
      }

      var sum = tco(function(x, y) {
        if (y > 0) {
          return sum(x + 1, y - 1)
        }
        else {
          return x
        }
      });

      sum(1, 100000)

      ---------------------------------------------------------

      tcoactive
      
      sumundefined
      accumulatedsum
      accumulatorwhile
      
    </pre>
  </div>
</template>
<script>
export default {
  mounted() {
    function test() {
      function trampoline(f) {
        while (f && f instanceof Function) {
          f = f();
        }
        return f;
      }

      function sum(x, y) {
        if (y > 0) {
          return sum.bind(null, x + 1, y - 1);
        } else {
          return x;
        }
      }

      console.log(trampoline(sum(1, 100000)));
    }

    // test();

    //
