<template>
  <div>
    <div class="title">
      <span>ProxyReflect</span>
      <div class="animation">
        <div class="move"></div>
      </div>
    </div>
    <h4>Proxy </h4>
    <pre>
 
      Proxy 
      meta programming

      Proxy 
      
      Proxy 

      ---------------------------------------------------------

      var obj = new Proxy({}, {
        get: function (target, key, receiver) {
          console.log(`getting ${key}!`);
          return Reflect.get(target, key, receiver);
        },
        set: function (target, key, value, receiver) {
          console.log(`setting ${key}!`);
          return Reflect.set(target, key, value, receiver);
        }
      });

      ---------------------------------------------------------

      getset
      obj

      ---------------------------------------------------------

      obj.count = 1
      //  setting count!
      ++obj.count
      //  getting count!
      //  setting count!
      //  2
    </pre>
    <el-divider></el-divider>
    <h4>Proxy </h4>
    <pre>
 
      ReflectProxyES6APIReflect

    1 ObjectObject.definePropertyReflect
          ObjectReflectReflect

    2 Object
          Object.defineProperty(obj, name, desc)
          Reflect.defineProperty(obj, name, desc)false

    3 ObjectObject
          name in objdelete obj[name]
          Reflect.has(obj, name)Reflect.deleteProperty(obj, name)
          
    4ReflectProxy
          ProxyReflect
          ProxyReflect
          ProxyReflect
    </pre>
    <el-divider></el-divider>
    <h4> Proxy </h4>
    <pre>
      Observer mode

      ---------------------------------------------------------

      const person = observable({
        name: '',
        age: 20
      });

      function print() {
        console.log(`${person.name}, ${person.age}`)
      }

      observe(print);
      person.name = '';
      // 
      // , 20

      ---------------------------------------------------------

      personprint
      print

       Proxy observableobserve
      observable Proxy 

      ---------------------------------------------------------

      const queuedObservers = new Set();

      const observe = fn => queuedObservers.add(fn);
      const observable = obj => new Proxy(obj, {set});

      function set(target, key, value, receiver) {
        const result = Reflect.set(target, key, value, receiver);
        queuedObservers.forEach(observer => observer());
        return result;
      }

      ---------------------------------------------------------

      Set
      observable
      set
    </pre>
  </div>
</template>
<script>
export default {
  mounted() {
    //----------------------------------------------------------
    //
    const queuedObservers = new Set();
    //
    const observe = fn => queuedObservers.add(fn);
    // Proxy 
    const observable = obj => new Proxy(obj, { set });

    function set(target, key, value, receiver) {
      const result = Reflect.set(target, key, value, receiver);
      queuedObservers.forEach(observer => observer());
      return result;
    }

    //----------------------------------------------------------
    const person = observable({
      name: "",
      age: 20
    });

    function printName() {
      console.log(`${person.name}`);
    }
    function printAge(params) {
      console.log(`${person.age}`);
    }

    observe(printName);
    observe(printAge);

    // person.name = "";

    function checkout(params) {
    
      if (arguments.length) {
        let type = Object.prototype.toString.call(params).slice(8, -1);
        switch (type) {
          case Number:
            type = isNaN(params)
              ? "NaN"
              : !isFinite(params)
              ? "Infinity"
              : type;
            break;
        }
      } else {
        return "";
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.title {
  position: sticky;
  top: 0;
  padding: 10px;
  height: 16px;
  line-height: 16px;
  background: #597ee7;
  text-align: center;
  color: #fff;
  border-radius: 18px;
  z-index: 2;
}
</style>


